.room-code{
    height:40px;
    border-radius: 8px;
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #835afd;
    cursor: pointer;
    display: flex;

    div{
        background-color: #835afd;
        padding: 0 12px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    span{
        display: block;
        align-self: center;
        flex: 1;
        padding: 0 16px 0px 12px;
        width: 220px;
        font-size: 14px;
        font-weight: 500;
    }
}